@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Playfair+Display&display=swap);

:root {
  --head-foot: #A0937D;
  --bk: #F2DAC3;
  --font: black;
  --preview: linear-gradient(to right, #00b09b, #96c93d);
  --edit: linear-gradient(to right, #2193b0, #6dd5ed);
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  width: 100%;
  min-height: 100vh;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  background-color: var(--bk);
  font-family: 'Lato', sans-serif;
}

.btn-box {
  position: relative;
  top: 5px;
  margin-top: 10px;
  display: flex;
}

.middle {
  height: 30px;
  width: 30px;
  position: absolute;
  top: 2.5px;
  left: 85px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  font-size: 12px;
  background-color: var(--bk);
  color: var(--font);
  -webkit-clip-path: polygon(50% 0%, 100% 50%, 50% 100%, 0% 50%);
          clip-path: polygon(50% 0%, 100% 50%, 50% 100%, 0% 50%);
}

.edit-btn, .preview-btn {
  cursor: pointer;
  width: 100px;
  height: 35px;
  border: none;
  color: var(--font);
}

.edit-btn.active {
  box-shadow: 0px 0px 5px 1px blue;
}

.preview-btn.active {
  box-shadow: 0px 0px 5px 1px green;
}

.edit-btn {
  border-bottom-left-radius: 10px;
  border-top-left-radius: 10px;
  background: var(--edit);
}

.preview-btn {
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
  background: var(--preview);
}
.header {
  width: 100%;
  height: 50px;
  background-color: var(--head-foot);
  display: flex;
  align-items: center;
}

.logo {
  font-family: 'Playfair Display', serif;
  margin-left: 5vw;
  color: var(--font);
  letter-spacing: 5px;
}

.theme {
  display: grid;
  place-items: center;
  cursor: pointer;
  margin-left: auto;
  margin-right: 5vw;
  width: 30px;
  height: 30px;
  color: var(--font);
  background-color: var(--head-foot);
  border: none;
  box-shadow: 2px 0px 5px 0px black;
  border-radius: 50%;
  font-size: 16px;
}

@media (max-width: 425px) {
  .logo {
    font-size: 20px;
  }
}
.preview {
  font-size: 14px;
  box-sizing: border-box;
  text-align: left;
  display: flex;
  align-items: center;
  color: rgb(231, 231, 231);
  width: 70%;
  min-height: 90vh;
  background-color: #DAE1E7;
  border: 1px solid black;
  margin-bottom: 40px;
}

.name {
  font-size: 2.2em;
}

.side {
  padding-left: 20px;
  width: 25%;
  background-color: #142850;
  height: 100%;
  min-height: 90vh;
}

.personal {
  width: 100%;
  margin-left: -20px;
  padding-left: 20px;
  display: flex;
  align-items: center;
  text-align: left;
  height: 50px;
  background-color: rgba(0, 0, 0, 0.5);
}

.main-content {
  margin-left: 10px;
  width: 68%;
  height: 100%;
  min-height: 90vh;
  color:rgb(39, 39, 39);
}

.info-box p {
  overflow: hidden;
  text-overflow: clip;
}

.exp, .educ {
  padding: 5px 0;
  width: 100%;
  min-height: 20px;
  border-top: 1px solid rgb(128, 128, 128);
  border-bottom: 1px solid rgb(128, 128, 128);
  margin-top: 20px;
}

.exp h3, .educ h3 {
  margin: 0;
  margin-left: 10px;
}

.item-box {
  display: flex;
  align-items: center;
}

.period {
  margin-left: 10px;
  width: 30%;
}

.period p, .m-text, .m-box p {
  margin: 0;
}

.m-text {
  font-size: 1.5em;
  font-weight: bold;
}

.m-box {
  margin-left: 20px;
}

@media (max-width: 845px) {
  .preview {
    font-size: 12px;
  }
}

@media (max-width: 768px) {
  .preview {
    width: 100%;
  }
}

@media (max-width: 510px) {
  .preview {
    font-size: 10px;
  }
}
.main {
  margin-bottom: 40px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  align-self: center;
  width: 70%;
  background-color: var(--head-foot);
  border-radius: 10px;
  box-shadow: 2px 0px 5px 0px rgb(77, 77, 77);
  color: var(--font);
}

.general,
.xp {
  margin: 10px 0px;
  width: 90%;
}

.form {
  display: flex;
  flex-direction: column;
}

.input {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 5px;
  width: 100%;
}

.input label {
  text-align: left;
  width: 20%;
}

.input input,
.input textarea {
  width: 87%;
  height: 30px;
  border: none;
  border-radius: 3px;
  background-color: var(--bk);
  color: var(--font);
}

.input textarea {
  min-height: 60px;
  resize: vertical;
}

.large-btn {
  margin-left: auto;
  width: 100%;
  height: 30px;
  color: var(--font);
  background-color: var(--bk);
  border-radius: 5px;
  border: none;
  cursor: pointer;
}

.m-vertical {
  margin: 10px 0;
}

@media (max-width: 768px) {
  .main {
    width: 100%;
  }

  .input label {
    width: 25%;
  }
}

@media (max-width: 425px) {
  .input label {
    font-size: 13px;
  }
}

@media (max-width: 375px) {
  .input label {
    width: 35%;
  }
}

.footer {
  background-color: var(--head-foot);
  margin-top: auto;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 50px;
  color: var(--font);
}

.logo-github {
  margin-left: 10px;
  width: 30px;
}
